package io.github.dennistsar.sirs_kobweb.components.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.backgroundColor
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxSize
import com.varabyte.kobweb.compose.ui.modifiers.transition
import com.varabyte.kobweb.compose.ui.modifiers.zIndex
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.base
import com.varabyte.kobweb.silk.components.style.toModifier
import org.jetbrains.compose.web.css.Color

val DarkBackgroundStyle = ComponentStyle.base("dark-background") {
    Modifier
        .fillMaxSize()
        .backgroundColor(Color.black)
        .transition("opacity .3s linear")
        .zIndex(150)
}

@Composable
fun DarkBackground(modifier: Modifier = Modifier) {
    Box(DarkBackgroundStyle.toModifier().then(modifier))
}