package io.github.dennistsar.sirs_kobweb.misc

fun String.isBlankOrNone(): Boolean = isBlank() || equals(None)


private val urlEncodings = listOf("," to "%2C", " " to "%20", "." to "%2E")

fun String.encodeURLParam(): String = urlEncodings.fold(this) { acc, (a, b) -> acc.replace(a, b) }

fun String.decodeURLParam(): String = urlEncodings.fold(this) { acc, (a, b) -> acc.replace(b, a) }


fun <T> Collection<T>.plusElementAtStart(element: T): List<T> = listOf(element) + this