package io.github.dennistsar.sirs_kobweb.states

import Entry
import TenQs
import androidx.compose.runtime.*
import getRatingAve
import getRatingStats
import getTotalRatings
import io.github.dennistsar.sirs_kobweb.misc.TenQsShortened
import io.github.dennistsar.sirs_kobweb.misc.jsFormatNum
import io.github.dennistsar.sirs_kobweb.misc.plusElementAtStart

@Stable
class ProfSummaryVM(
    entriesByProf: Map<String, List<Entry>>,
    selectedProf: String,
    deptCode: String,
    deptUrl: String,
    getCourseUrl: (String) -> String,
    goToDeptData: () -> Unit,
    goToCourseData: (String) -> Unit,
) {
    private val profEntries = entriesByProf[selectedProf] ?: throw IllegalArgumentException("Prof not found")

    var selectedQ by mutableStateOf(8)
    val selectedQText by derivedStateOf { TenQs[selectedQ] }
    val dropDownTexts = TenQsShortened

    var selectedCourse by mutableStateOf(0)
    val courses = profEntries
        .map { it.course }
        .distinct()
        .sorted()
        .plusElementAtStart("All Courses")

    val graphNums by derivedStateOf {
        profEntries.run {
            if (selectedCourse != 0)
                filter { it.course == courses[selectedCourse] }
            else this
        }.getTotalRatings()[selectedQ]
    }
    val graphLabel by derivedStateOf {
        if (selectedQ < 8) "Disagree -> Agree" else "Poor -> Excellent"
    }

    val average by derivedStateOf { jsFormatNum(graphNums.getRatingAve(), minDecDigits = 2, maxDecDigits = 2) }
    val numResponses by derivedStateOf { graphNums.sum() }

    private val deptAveComparison by derivedStateOf {
        val deptAves = entriesByProf
            .map { (_, entries) ->
                entries.getTotalRatings().map { it.getRatingStats() }
            }
        AveComparisonData(
            jsFormatNum(deptAves.map { it[selectedQ].first }.average(), minDecDigits = 2, maxDecDigits = 2),
            deptAves.size.toString(),
            deptUrl,
            deptCode,
        ) { goToDeptData() }
    }

    private val allProfCourseStats = entriesByProf.map { (_, entries) ->
        entries.groupBy { it.course }
            .filterKeys { it in courses }
            .mapValues { (_, entries) -> entries.getTotalRatings() }
    }
    private val courseAveComparison by derivedStateOf {
        courses[selectedCourse].takeIf { selectedCourse != 0 }?.let {
            val courseAve = allProfCourseStats.mapNotNull { ratingsByCourse ->
                val ratings = ratingsByCourse[it] ?: return@mapNotNull null
                ratings[selectedQ].getRatingStats().first
            }
            AveComparisonData(
                jsFormatNum(courseAve.average(), minDecDigits = 2, maxDecDigits = 2),
                courseAve.size.toString(),
                getCourseUrl(it),
                "$deptCode:$it",
            ) { goToCourseData(it) }
        }
    }

    val aveComparison by derivedStateOf { courseAveComparison ?: deptAveComparison }
}

data class AveComparisonData(
    val average: String,
    val totalNum: String,
    val url: String,
    val urlText: String,
    val onLinkClick: () -> Unit,
)