package io.github.dennistsar.sirs_kobweb.states

import Entry
import Instructor
import School
import Semester
import SemesterType
import androidx.compose.runtime.*
import getAvesAndTotal
import getTotalRatings
import io.github.dennistsar.sirs_kobweb.misc.*
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.launch
import remote.GithubSource
import substringAfterBefore

enum class Status {
    InitialLoading, Dept, Course, Prof,
}

@Stable
class DataPageVM(
    private var repository: GithubSource,
    private var coroutineScope: CoroutineScope,
    initialSchool: String?,
    initialDept: String?,
    initialCourse: String?,
    initialProf: String?,
) {
    var state: DataPageState by mutableStateOf(DataPageState())
        private set

    private var schoolMap: Map<String, School> by mutableStateOf(emptyMap())
    private var instructorsMap: Map<String, List<String>> by mutableStateOf(emptyMap())
    private var deptMap: Map<String, String> by mutableStateOf(emptyMap())
    private var allEntriesByProf: Map<String, List<Entry>> by mutableStateOf(emptyMap())
    private var allInstructors: List<Instructor> by mutableStateOf(emptyList())

    private var initialLoading by mutableStateOf(true)
    var pageLoading by mutableStateOf(false)

    var minSem by mutableStateOf(Semester(SemesterType.Spring, 2018).numValue)
    val semBounds = Semester(SemesterType.Spring, 2014).numValue to
            Semester(SemesterType.Spring, 2022).numValue

    private val entriesByProf by derivedStateOf {
        val semesterFilter = Semester(minSem)
        allEntriesByProf.filter {
            it.value.last().semester >= semesterFilter // because entries should be sorted in ascending order
        }
    }

    val url: String get() = getUrl()

    val status
        get() = when {
            // schoolMap.isEmpty() can work for initialLoading but doesn't account for dept/prof loading
            initialLoading -> Status.InitialLoading
            !state.profState.selected.isBlankOrNone() -> Status.Prof
            !state.courseState.selected.isBlankOrNone() -> Status.Course
            else -> Status.Dept
        }

    val mapToDisplay: Map<String, List<String>>? by derivedStateOf {
        when (status) {
            Status.Dept -> entriesByProf.toDisplayMap()
            Status.Course -> entriesByProf.mapValues { (_, v) ->
                v.filter { it.course == state.courseState.selected }
            }.filterValues { it.isNotEmpty() }.toDisplayMap()

            else -> null
        }
    }

    val teachingInstructors: List<String> by derivedStateOf {
        val courseString = with(state) { "${schoolState.selected}:${deptState.selected}:${courseState.selected}" }
        instructorsMap[courseString].takeIf { status == Status.Course } ?: emptyList()
    }

    val pageTitle by derivedStateOf {
        when (status) {
            Status.InitialLoading -> ""
            Status.Prof -> with(state) { "${profState.selected} (${getCode()})" }
            Status.Dept -> deptMap[state.deptState.selected]?.let { "$it (${getCode()})" }
                ?: throw IllegalStateException("Invalid dept")

            else -> getCode()
        }
    }

    inner class SearchBarVM {
        var searchBoxInput by mutableStateOf("")
        var searchBarClickedOnce by mutableStateOf(false)

        val searchBarSuggestions by derivedStateOf {
            // to not slow down initial page load, add suggestions to DataList after search bar is initially clicked
            if (!searchBarClickedOnce) emptyList()
            else deptList + profList
        }

        private val profList by derivedStateOf {
            allInstructors
                .filter { it.latestSem >= Semester(minSem) }
                .map { "${it.name} (${it.school}:${it.dept})" }
        }

        private val deptList = schoolMap.flatMap { (code, school) ->
            school.depts.map { "$code:$it - ${deptMap[it]}" }
        }

        fun onEnterSearch() {
            val input = searchBoxInput
            searchBoxInput = ""
            // valid states: "SMITH, JOHN (01:198)", "01:198 - Computer Science ", "01:198", "01:198:112"
            val school = input.substringAfterBefore("(", ":")
            val dept = input.substringAfterBefore(":", ")")

            if (input.contains("(")) { // assuming no depts have "(" in them - dangerous/wrong?
                val prof = input.substringBefore(" (")
                onSelectSchool(school = school, dept = dept, prof = prof)
            } else {
                val course = dept.substringAfter(":", None)
                val newDept = dept.substringBefore(":").substringBefore(" ")
                    .also { if (schoolMap[school]?.depts?.contains(it) != true) return }
                onSelectSchool(school = school, dept = newDept, course = course)
            }
        }
    }

    lateinit var searchBarVM: SearchBarVM
        private set

    fun onSelectSchool(
        school: String?,
        dept: String? = null,
        course: String? = null,
        prof: String? = null,
    ) {
        (schoolMap[school] ?: schoolMap["01"])?.let {
            val newDept = dept.takeIf { i -> i in it.depts } ?: it.depts.first()
            onSelectDept(dept = newDept, school = it, course = course, prof = prof)
        } ?: throw IllegalStateException("01 School Not Found")
    }

    fun onSelectDept(
        dept: String,
        school: School = schoolMap[state.schoolState.selected]
            ?: throw IllegalStateException("Selected School Not Found"),
        course: String? = null,
        prof: String? = null,
    ) {
        if (!isSameAsState(school.code, dept, course, prof)) return
        pageLoading = true
        coroutineScope.launch {
            // prevent unnecessary network requests & calculations
            val newDept = state.schoolState.selected != school.code || state.deptState.selected != dept
            if (newDept)
                allEntriesByProf = repository.getLatestEntriesByProfInDept(school.code, dept)

            val profList = if (newDept)
                entriesByProf.keys.sorted().plusElementAtStart(None)
            else state.profState.list

            val courseList = if (newDept)
                entriesByProf.values.flatMap { it.map(Entry::course) }
                    .toSet().sorted().plusElementAtStart(None)
            else state.courseState.list

            val courseValid = course in courseList
            val oldState = state
            state = with(state) {
                copy(
                    schoolState = schoolState.copy(schoolMap.values, school.code),
                    deptState = deptState.copy(school.associateDeptsToName(), dept),
                    courseState = courseState.copy(
                        courseList,
                        course.takeIf { courseValid } ?: None,
                    ),
                    profState = profState.copy(
                        profList,
                        prof.takeIf { !courseValid && it in profList } ?: None,
                    ),
                )
            }
            initialLoading = false
            if (oldState == state) pageLoading = false // must be done manually since screen won't recompose
        }
    }

    fun onSelectCourse(course: String) {
        state = with(state) {
            copy(
                courseState = courseState.copy(selected = course),
                profState = profState.copy(selected = if (!course.isBlankOrNone()) None else profState.selected)
            )
        }
    }

    fun onSelectProf(prof: String) {
        state = with(state) {
            copy(
                profState = profState.copy(selected = prof),
                courseState = courseState.copy(selected = if (!prof.isBlankOrNone()) None else courseState.selected)
            )
        }
    }

    val profSummaryVM: ProfSummaryVM? by derivedStateOf {
        if (status != Status.Prof)
            null
        else {
            ProfSummaryVM(
                entriesByProf,
                state.profState.selected,
                getCode(course = None),
                getUrl(prof = None),
                { getUrl(prof = None, course = it) },
                { onSelectProf(None) },
                { onSelectCourse(it) }
            )
        }
    }

    init {
        coroutineScope.launch {
            schoolMap = repository.getDirSchoolMap("data-9-by-prof")
            instructorsMap = repository.getLatestInstructors()
            deptMap = repository.getDeptMap()
            allInstructors = repository.getAllInstructors()
            searchBarVM = SearchBarVM()
            onSelectSchool(school = initialSchool, dept = initialDept, course = initialCourse, prof = initialProf)
        }
    }

    fun onPopState(params: String) {
        params.drop(1).split('&').associate {
            it.split('=', limit = 2).zipWithNext().getOrNull(0) ?: return
        }.let {
            val school = it["school"].takeIf { i -> i != "" } ?: return
            val dept = it["dept"].takeIf { i -> i != "" } ?: return
            val course = it["course"]
            val prof = it["prof"]?.decodeURLParam()

            // need to figure out how to deal wih invalid course/prof on pop -maybe?
            when {
                school != state.schoolState.selected ->
                    onSelectSchool(school = school, dept = dept, course = course, prof = prof)

                dept != state.deptState.selected ->
                    onSelectDept(dept = dept, course = course, prof = prof)

                course != null -> onSelectCourse(course)
                prof != null -> onSelectProf(prof)
                else -> {
                    if (state.courseState.selected != None) onSelectCourse(None)
                    else if (state.profState.selected != None) onSelectProf(None)
                }
            }
        }
    }

    private fun getUrl(
        school: String = state.schoolState.selected,
        dept: String = state.deptState.selected,
        course: String = state.courseState.selected,
        prof: String = state.profState.selected,
    ): String {
        return "?" +
                "school=$school" +
                "&dept=$dept" +
                with(course) { if (isBlankOrNone()) "" else "&course=$this" } +
                with(prof) { if (isBlankOrNone()) "" else "&prof=${encodeURLParam()}" }
    }

    private fun getCode(
        school: String = state.schoolState.selected,
        dept: String = state.deptState.selected,
        course: String = state.courseState.selected,
    ): String {
        return "$school:$dept" + with(course) { if (isBlankOrNone()) "" else ":$this" }
    }

    private fun isSameAsState(school: String, dept: String, course: String?, prof: String?): Boolean {
        return school != state.schoolState.selected ||
                dept != state.deptState.selected ||
                (course ?: None) != state.courseState.selected ||
                (prof ?: None) != state.profState.selected
    }

    private fun School.associateDeptsToName(): List<Pair<String, String>> {
        return depts.associateWith { deptMap[it] ?: throw IllegalStateException("Invalid Dept") }.toList()
    }
}

private fun Map<String, List<Entry>>.toDisplayMap(addAverage: Boolean = true): Map<String, List<String>> {
    return mapValues { (_, entries) ->
        entries.getTotalRatings().subList(0, 10).getAvesAndTotal()
    }.run { // add Average
        if (!addAverage || size == 1) this
        else {
            val stats = this.values.reduce { acc, pair ->
                acc.first.zip(pair.first) { a, b -> a + b } to acc.second + pair.second
            }
            val ave = stats.first.map { it / this.size }
            plus("Average" to Pair(ave, stats.second))
        }
    }.mapValues { (_, nums) ->
        nums.first.map { jsFormatNum(it, minDecDigits = 2, maxDecDigits = 2) } +
                nums.second.toString()
    }
}