val SpanTextHeaderVariant = SpanTextStyle.addVariant("header-responsive") {
    base {
        Modifier
            .fontWeight(FontWeight.Bold)
            .smallCapsFont()
            .fontSize(1.5.cssRem)
            .lineHeight(1.75.cssRem)
    }
    Breakpoint.MD {
        Modifier
            .fontSize(2.25.cssRem)
            .lineHeight(2.5.cssRem)
    }
}

val HeaderStyle = ComponentStyle("header") {
    base { Modifier.width(95.percent) }
    Breakpoint.MD { Modifier.width(70.percent) }
}

val QuestionSelectVariant = SelectStyle.addVariant("question-select") {
    base {
        Modifier.fontSize(1.cssRem).width(90.percent)
    }
    Breakpoint.MD {
        Modifier.styleModifier { property("width", "unset") }
    }
}

@Composable
fun ProfSummary(viewModel: ProfSummaryVM) {
    CourseButtonsBar(viewModel.courses, viewModel.selectedCourse) { viewModel.selectedCourse = it }

    key(viewModel.selectedQ) { // needed for jsBalanceTextById() to work upon text change
        val id = "survey-q"
        Box(HeaderStyle.toModifier(), Alignment.Center) {
            SpanText(
                "\"${viewModel.selectedQText}\"",
                Modifier.id(id),
                SpanTextHeaderVariant,
            )
            SideEffect {
                jsBalanceTextById(id)
            }
        }
    }

    CustomDropDown(
        hint = "Choose another question...",
        selectVariant = QuestionSelectVariant,
        list = viewModel.dropDownTexts,
        onSelect = { viewModel.selectedQ = viewModel.dropDownTexts.indexOf(it) },
    )

    // displayIf / displayUntil only shows one of these
    ProfStatsMobile(viewModel)
    ProfStatsDesktop(viewModel)
}

@Composable
fun CourseButtonsBar(courses: List<String>, selectedCourse: Int, onClick: (Int) -> Unit) {
    Box(Modifier.alignSelf(KobAlignSelf.Start)) { // needed as non-scrollable parent
        // fixes issue on mobile where scrollbar appears too high
        val mobileScrollbarOffset = 0.5.cssRem
        Row(
            Modifier
                .maxWidth(100.percent)
                .padding(bottom = mobileScrollbarOffset)
                .margin(bottom = -mobileScrollbarOffset)
                .overflowX(Overflow.Auto)
                .flexWrap(FlexWrap.Nowrap)
                .fontSize(1.3.cssRem)
                .gap(.5.cssRem),
        ) {
            courses.forEachIndexed { index, course ->
                PillButton(
                    index == selectedCourse,
                    course,
                    Modifier.flexShrink(0),
                ) { onClick(index) }
            }
        }
    }
}