package io.github.dennistsar.sirs_kobweb.components.widgets

import androidx.compose.runtime.Composable
import androidx.compose.runtime.LaunchedEffect
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.attrsModifier

@Composable
fun TransitionObject(
    startTransition: () -> Unit,
    onEndTransition: () -> Unit,
    content: @Composable (Modifier) -> Unit,
) {
    content(
        Modifier.attrsModifier {
            addEventListener("transitionend") { onEndTransition() }
        }
    )
    LaunchedEffect(Unit) { startTransition() }
}