package io.github.dennistsar.sirs_kobweb.components.sections

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.icons.fa.FaGithub
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.base
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.SilkTheme
import io.github.dennistsar.sirs_kobweb.misc.goatCounterClick
import org.jetbrains.compose.web.css.AlignSelf
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px

val FooterStyle = ComponentStyle.base("footer") {
    Modifier
        .margin(top = 2.cssRem)
        .borderTop(1.px, LineStyle.Solid, SilkTheme.palettes[colorMode].border)
        .padding(topBottom = 1.cssRem, leftRight = 2.cssRem)
        .alignSelf(AlignSelf.Center)
        .textAlign(TextAlign.Center)
        .rowGap(.1.cssRem)
}

@Composable
fun Footer(modifier: Modifier = Modifier) {
    Column(
        FooterStyle.toModifier().then(modifier),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        SpanText("Not affiliated with Rutgers University")
        Row(verticalAlignment = Alignment.CenterVertically) {
            FaGithub()
            SpanText(" This site is ")
            Link(
                "https://github.com/DennisTsar/RU-SIRS",
                "open source",
                Modifier.goatCounterClick("gh-link", title = "GitHub Link")
            )
        }
        Row {
            SpanText("Made with ")
            Link(
                "https://github.com/varabyte/kobweb",
                "Kobweb",
                Modifier.goatCounterClick("kobweb-link", title = "Kobweb Link")
            )
        }
    }
}