val PageTitleStyle = ComponentStyle("page-title") {
    base {
        Modifier
            .alignSelf(AlignSelf.Start)
            .textAlign(TextAlign.Left)
            .fontSize(2.cssRem)
            .fontWeight(FontWeight.Bold)
    }
    Breakpoint.MD { Modifier.minHeight(75.px) }
}

@Page
@Composable
fun Data() {
    // region non-UI setup
    val ctx = rememberPageContext()
    val coroutineScope = rememberCoroutineScope()
//    val colorMode by rememberColorMode()
    val originalOnPopState = remember { window.onpopstate } // need to use this to avoid infinite recursion
    val repository = remember { GithubSource() }

    var navOpenMobile by remember { mutableStateOf(false) }
    var mobileBackOpacity by remember { mutableStateOf(0.0) }
    var mobileNavPosition by remember { mutableStateOf(-1000) }
    val closeMobileNav = remember {
        {
            mobileBackOpacity = 0.0
            mobileNavPosition = -1000
        }
    }

    val viewModel = remember {
        DataPageVM(
            repository = repository,
            coroutineScope = coroutineScope,
            initialSchool = ctx.params["school"],
            initialDept = ctx.params["dept"],
            initialCourse = ctx.params["course"],
            initialProf = ctx.params["prof"]?.decodeURLParam()?.uppercase(),
        )
    }

    DisposableEffect(Unit) {
        window.onpopstate = {
            originalOnPopState?.run { invoke(it) } // keeps default behavior when going back to other page
            viewModel.onPopState(window.location.search)
        }
        onDispose {
            window.onpopstate = originalOnPopState
        }
    }

    val status = viewModel.status

    remember(viewModel.url) {
        if (status != Status.InitialLoading) {
            ctx.router.tryRoutingTo(viewModel.url)
            jsGoatCount()
        }
    }

    remember(viewModel.pageTitle) {
        document.title = viewModel.pageTitle
    }
    // endregion

    Row(
        Modifier
            .fillMaxSize()
            .flexWrap(FlexWrap.Nowrap)
            .background("linear-gradient(180deg, rgba(247,202,208,1) 0%, rgba(237,242,244,1) 55%, rgba(214,224,236,1) 100%)")
    ) {
        Column(
            SideNavStyle.toModifier()
                .rowGap(0.5.cssRem)
                .displayIf(Breakpoint.MD),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            if (viewModel.status == Status.InitialLoading) return@Column

            MainNav(viewModel)

            var showMore by remember { mutableStateOf(false) }
            Row(
                Modifier
                    .columnGap(0.5.cssRem) // for space between text and icon
                    .cursor(Cursor.Pointer)
                    .onClick { showMore = !showMore },
                verticalAlignment = Alignment.CenterVertically
            ) {
                Text("Show ${if (showMore) "less" else "more"} options")
                if (showMore) FaCaretUp(Modifier.padding(top = .25.cssRem)) else FaCaretDown()
            }

            if (showMore) {
                Column(
                    Modifier
                        .backgroundColor(Colors.Black)
                        .borderRadius(12.px)
                        .padding(1.cssRem),
                    horizontalAlignment = Alignment.CenterHorizontally,
                ) {
                    Text("Hide profs with no data after")
                    var rangeValue by remember { mutableStateOf(viewModel.minSem) }
                    RangeInput(
                        value = rangeValue,
                        min = viewModel.semBounds.first,
                        max = viewModel.semBounds.second,
                    ) {
                        onChange { event ->
                            event.value?.let { viewModel.minSem = it.toInt() }
                        }
                        onInput { event ->
                            event.value?.let { rangeValue = it.toInt() }
                        }
                    }
                    Text("${if (rangeValue % 2 == 0) "Spring" else "Fall"} ${rangeValue / 2}")
                }
            }

            Spacer()
            Footer()
        }

        if (navOpenMobile) {
            TransitionObject(
                startTransition = {
                    mobileBackOpacity = .5
                    mobileNavPosition = 0
                },
                onEndTransition = { if (mobileBackOpacity == 0.0) navOpenMobile = false }
            ) {
                if (viewModel.status != Status.InitialLoading) {
                    MainNav(viewModel, MobileNavStyle.toModifier().top(mobileNavPosition.px))
                }
                DarkBackground(
                    Modifier
                        .opacity(mobileBackOpacity)
                        .onClick { closeMobileNav() }
                        .position(Position.Fixed)
                        .then(it)
                )
            }
        }

        MobileNavButton {
            navOpenMobile = true
        }

        Column(
            modifier = Modifier
                .fillMaxHeight()
                .maxWidth(100.vw) // maybe
                .padding(1.cssRem)
                .rowGap(0.5.cssRem)
                .textAlign(TextAlign.Center)
                .boxShadow(
                    offsetX = 10.px,
                    offsetY = 5.px,
                    blurRadius = 30.px,
                    spreadRadius = 0.px,
                    color = Color.rgba(0, 0, 0, .1f),
                    inset = true,
                ).flexGrow(1),
            horizontalAlignment = Alignment.CenterHorizontally,
        ) {
            key(viewModel.profSummaryVM, viewModel.mapToDisplay) {
                closeMobileNav()
            }

            Row(
                PageTitleStyle.toModifier(),
                verticalAlignment = Alignment.CenterVertically
            ) {
                Text(viewModel.pageTitle.substringBefore(" ("))
                if (viewModel.pageTitle.contains("(")) {
                    SpanText(
                        " (" + viewModel.pageTitle.substringAfter(" ("),
                        Modifier.displayIf(Breakpoint.MD)
                    )
                }
                if (viewModel.status == Status.InitialLoading || viewModel.pageLoading)
                    LoadingSpinner()
            }

            viewModel.profSummaryVM?.let { ProfSummary(it) }
            viewModel.mapToDisplay?.let {
                key(it.size / keyReset) {
                    ProfScoresList(
                        it,
                        viewModel.teachingInstructors,
                        viewModel::onSelectProf
                    )
                }
            }
            viewModel.pageLoading = false
        }
    }
}