package io.github.dennistsar.sirs_kobweb.components.sections.dataPage

import androidx.compose.runtime.Composable
import androidx.compose.runtime.key
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.base
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.toSilkPalette
import io.github.dennistsar.sirs_kobweb.components.widgets.CustomDropDown
import io.github.dennistsar.sirs_kobweb.components.widgets.Logo
import io.github.dennistsar.sirs_kobweb.misc.keyReset
import io.github.dennistsar.sirs_kobweb.states.DataPageVM
import org.jetbrains.compose.web.css.*

val SideNavStyle = ComponentStyle.base("side-nav") {
    Modifier
        .position(Position.Sticky)
        .flex(0, 0, 325.px)
        .top(0.px)
        .height(100.vh)
        .zIndex(100)
        // Intentionally invert the header colors from the rest of the page
        .backgroundColor(colorMode.toSilkPalette().color)
        .color(colorMode.toSilkPalette().background)
}

val MobileNavStyle = ComponentStyle.base("mobile-nav") {
    Modifier
        .position(Position.Fixed)
        .left(0.px)
        .right(0.px)
        .bottom(0.px)
        .margin(30.px)
        .zIndex(1000)
        .styleModifier { property("height", "fit-content") }
        // Intentionally invert the header colors from the rest of the page
        .backgroundColor(colorMode.toSilkPalette().color)
        .color(colorMode.toSilkPalette().background)
        .borderRadius(8.px)
        .transition("top .35s ease")
}

@Composable
fun MainNav(viewModel: DataPageVM, modifier: Modifier = Modifier) {
    val labelModifier = Modifier
        .fontWeight(FontWeight.Bold)
        .fontSize(1.1.cssRem)

    Column(
        modifier
            .padding(1.cssRem)
            .minWidth(0.px),
        horizontalAlignment = Alignment.CenterHorizontally,
    ) {
        Logo(
            Modifier
                .width(75.percent)
                .padding(topBottom = 0.25.cssRem)
                .alignSelf(AlignSelf.Start)
        )

        SearchForm(viewModel.searchBarVM)

        Column(
            Modifier.rowGap(.3.cssRem),
            horizontalAlignment = Alignment.CenterHorizontally,
        ) {

            SpanText("School", labelModifier)
            CustomDropDown(
                list = viewModel.state.schoolState.list,
                onSelect = { viewModel.onSelectSchool(it) },
                selectModifier = Modifier.fillMaxWidth(),
                getText = { "${it.code} - ${it.name}" },
                getValue = { it.code },
                selected = viewModel.state.schoolState.selected,
            )

            SpanText("Department", labelModifier)
            CustomDropDown(
                list = viewModel.state.deptState.list,
                onSelect = { viewModel.onSelectDept(it) },
                selectModifier = Modifier.fillMaxWidth(),
                getText = { it.second }, // consider whether to include code
                getValue = { it.first },
                selected = viewModel.state.deptState.selected,
            )

            SpanText("Course (Optional)", labelModifier)
            key(viewModel.state.courseState.list.size / keyReset) {
                CustomDropDown(
                    list = viewModel.state.courseState.list,
                    onSelect = { viewModel.onSelectCourse(it) },
                    selectModifier = Modifier.width(25.percent),
                    selected = viewModel.state.courseState.selected,
                )
            }

            SpanText("Prof (Optional)", labelModifier)
            key(viewModel.state.profState.list.size / keyReset) {
                CustomDropDown(
                    list = viewModel.state.profState.list,
                    onSelect = { viewModel.onSelectProf(it) },
                    optionModifier = Modifier.width(75.percent),
                    selected = viewModel.state.profState.selected,
                )
            }
        }
    }
}